<template>
  <div class="vod_play">
    <!--  -->
    <title-bar>{{detailInfo.vod_name}}</title-bar>

    <!--  -->
    <div class="main">
      <video-player class="video-player vjs-custom-skin"
        ref="videoPlayer"
        :playsinline="true"
        :options="playerOptions">
      </video-player>
    </div>
    <!--  -->
    <div class="vod_downl">
      <div>
        <i class="icon iconfont icon-home1"></i>
        <p></p>
      </div>
      <a>
        <i class="icon iconfont icon-xiazai"></i>
        <p></p>
      </a>
      <div>
        <i class="icon iconfont icon-dashang"></i>
        <p></p>
      </div>
    </div>
    <!--  -->
    <div class="series">
      <span></span>
      <van-tabs
      v-model="active"
      class="series_tab"
      color="#5351ca80"
      @click="getTabUrl">
        <van-tab
        :title="item"
        v-for="(item, index) in playFrom"
        :key="index">
        </van-tab>
      </van-tabs>
      <ul class="vod_numb">
            <li v-for="(i, index) in urlObj"
            :key="index"
            :class="[selIndex == index? 'selIndex' : 'indexList']"
            @click="selVodNumb(index,i)">{{i.type}}</li>
          </ul>
    </div>
    <!--  -->
    <div class="post-commnet">
     <p class="commnet_title"></p>
      <TextOverflow :content="detailInfo.vod_blurb"></TextOverflow>   
    </div>
    </div>
</template>

<script>
import TextOverflow from '../components/TextOverflow'
import TitleBar from '../components/DoHeader'
import DoPost from '../components/DoPost'
export default {
  components: {
    TitleBar,
    TextOverflow,
    DoPost
  },
  data () {
    return {
      detailInfo: this.$store.state.detailInfo,
      active: 0, //tab
      selIndex: 0, //
      playerOptions: {
      playbackRates: [0.5, 1.0, 1.5, 2.0], // 
      autoplay: true, // true,
      muted: false, // 
      loop: false, // 
      preload: 'auto', // <video>auto,
      language: 'zh-CN',
      aspectRatio: '16:9', //  - "16:9""4:3"
      fluid: true, // trueVideo.js player
      sources: [{
       type: "application/x-mpegURL", // 
       src: '' // url
      }],
      hls: true,
      poster: '', // 
      notSupportedMessage: '', // Video.js
      controlBar: {
        timeDivider: true, // 
        durationDisplay: true, // 
        remainingTimeDisplay: false, // 
        fullscreenToggle: true // 
      }
    },
    playUrl: '', //
    playFrom: '', //
    urlObj: [] //
    }
  },
  created () {
    // console.log(this.detailInfo);
    this.getTabUrl(0)
  },
  computed: {
    actor (value) {
      if (this.detailInfo.vod_actor == '') {
        return ''
      }
      return this.detailInfo.vod_actor
    }
  },
  methods: {

    getTabUrl (index) {
      var url1 = []
      this.urlObj = []
      this.playFrom = this.detailInfo.vod_play_from.split('$$$')
      this.playUrl = this.detailInfo.vod_play_url.split('$$$') //
     url1 = this.playUrl[index].split('#') //
     url1.map(item => {  
      this.urlObj.push({
        type: item.split('$')[0],
        url: item.split('$')[1]
      })
    })
    this.selVodNumb(0,this.urlObj[0])
    },
    selVodNumb (index,i) {
      this.selIndex = index
      
      this.playerOptions.sources[0].src = i.url
      console.log(this.playerOptions.sources[0].src);
    }
  },
  watch: {
    "$store.state.detailInfo": function () {
      this.detailInfo = this.$store.state.detailInfo
    },
    "playerOptions.sources.src": function () {
      
    }
  }
}
</script>

<style lang="scss" scoped>
.vod_play {
  margin-bottom: 48px;
  .vod_downl{
    display: flex;
    width: 100%;
    height: 60px;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    div,a{
      width: 60px;
      height: 60px;
      text-align: center;
    }
    i{
      font-size: 24px;
    }
    a{
      margin: 0 10px;
      color: #333;
    }
  }
  .post-commnet {
    width: 100%;
    padding: 10px;
    .commnet_title{
      font-size: 14px;
      line-height: 28px;
      font-weight: 700;
      border-bottom: 1px solid oldlace;
      margin-bottom: 5px;
    }
  }
  .text_title{
    width: 100%;
    height: 30px;
    line-height: 30px;
    background: #fff;
    padding-left: 10px;
    font-size: 18px;
  }
  .series{
    padding: 10px;
    font-size: 15px;
    .series_tab{
      margin: 0 10px;
    }
    .vod_numb{
      font-size: 10px;
      display: flex;
      overflow: scroll;
      flex-wrap: wrap;
      margin-top: 10px;
      // align-content: space-around;
      .indexList{
        padding: 5px 10px;
        text-align: center;
        margin: 5px 5px;
        border: 1px solid #333;
      }
      .selIndex{
        padding: 5px 10px;
        text-align: center;
        margin: 5px 5px;
        background: #5351ca80;
        color: #fff;
      }
    }
  }
}
</style>
